
.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    padding: 40px 20px;
}

.image{
    border-radius: 5%;
}

hr{
    width: 30%;
    background-color: #303030;
}

h2{
    font-weight: normal;
    margin-bottom: 10px;
}
